import request from '@/utils/axios'

//新增飞机
export function add(params) {
    return request({
        url: '/plane/add',
        method: 'post',
        params
    })
}

//列表
export function planeList(params) {
    return request({
      url:'/common/Plane/search',
      method: 'post',
      params
    })
  }

  //编辑
export function edit(params) {
    return request({
      url:'/plane/modify',
      method: 'post',
      params
    })
  }



  