<template>
  <div class="fillcontain">
    <h3>飞机管理</h3>
    <div class="contain">
      <div class="table_container">
        <!-- <el-row>
          <el-col :span="24">
            <div class="readWrapper">
              
            </div>
          </el-col>
        </el-row>-->
        <el-button class="search addPlane" type="primary" @click="addBut(1)">新增</el-button>
        <el-table
          v-loading="loading"
          :data="tableData"
          border
          stripe
          highlight-current-row
          header-cell-class-name="table-header-class"
          style="width:100%"
        >
          <el-table-column label="序号" align="center">
            <template slot-scope="scope">
              <span>{{scope.$index+(paginations.pageIndex-1) * paginations.pageSize + 1}}</span>
            </template>
          </el-table-column>
          <el-table-column property="fid" label="飞机ID" align="center"></el-table-column>
          <el-table-column property="name" label="飞机版本" align="center"></el-table-column>
          <el-table-column property="state" label="飞机状态" :formatter="formatState" align="center"></el-table-column>
          <el-table-column property="time" label="创建时间" :formatter="formatTime" align="center"></el-table-column>
          <el-table-column fixed="right" label="操作">
            <template slot-scope="scope">
              <i @click="handleClick(scope.row)" class="el-icon-edit userBut"></i>
              <!-- <i @click="editClick(scope.row)" class="el-icon-s-tools userBut" slot="reference"></i> -->
              <el-popconfirm title="状态改为正常吗？" @onConfirm="deleteOk(scope.row,1)">
                <i class="el-icon-setting userBut" slot="reference"></i>
              </el-popconfirm>
              <el-popconfirm title="确定维修吗？" @onConfirm="deleteOk(scope.row,2)">
                <i class="el-icon-s-tools userBut" slot="reference"></i>
              </el-popconfirm>
              <el-popconfirm title="确定删除吗？" @onConfirm="deleteOk(scope.row,0)">
                <i class="el-icon-delete userBut" slot="reference"></i>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
        <el-row>
          <el-col :span="24">
            <div class="pagination">
              <el-pagination
                v-if="paginations.total > 0"
                :page-sizes="paginations.pageSizes"
                :page-size="paginations.pageSize"
                :layout="paginations.layout"
                :total="paginations.total"
                :current-page="paginations.pageIndex"
                @current-change="handleCurrentChange"
                @size-change="handleSizeChange"
              ></el-pagination>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <el-dialog
      :title="title"
      :visible.sync="dialogFormVisible"
      id="customWidth"
      class="customWidth"
    >
      <el-form :model="form">
        <el-form-item label="飞机版本：" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="飞机ID：" :label-width="formLabelWidth">
          <el-input v-model="form.fid" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="飞机状态：" :label-width="formLabelWidth">
          <el-select class="fjzt" v-model="form.state" placeholder="请选择">
            <el-option
              v-for="item in optionsFly"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="search userButModle" @click="dialogFormVisible = false">取 消</el-button>
        <el-button class="search userButModle" type="primary" @click="add">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { planeList, add, edit } from "@/api/plane";
export default {
  data() {
    return {
      fileList: [],
      title: "新增飞机",
      optionsFly: [
        {
          value: "2",
          label: "维修"
        },
        {
          value: "1",
          label: "正常"
        },
        {
          value: "3",
          label: "进厂"
        }
      ],
      form: {
        name: "",
        fid: "",
        state: ""
      },
      dialogFormVisible: false,
      formLabelWidth: "120px",
      formLabelWidth2: "120px",
      tableData: [],
      loading: false,
      addType: 0,
      paginations: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "total, sizes, prev, pager, next, jumper"
      }
    };
  },
  mounted() {
    this.planeList({
      count: this.paginations.pageSize,
      index: this.paginations.pageIndex,
      search: {
        state: {
          $gt: 0
        }
      }
    });
  },
  methods: {
    addBut(type) {
      this.title = "新增飞机";
      for (var i in this.form) {
        this.form[i] = "";
      }
      this.addType = 0;
      this.dialogFormVisible = true;
    },

    deleteOk(row, type) {
      var del = {
        _id: row._id,
        data: {
          state: type
        }
      };
      edit(del).then(res => {
        if (res.code == 0) {
          this.$message({
            message: type == 2 ? "飞机状态修改成功！" : "飞机状态修改成功！",
            type: "success"
          });
          this.planeList({
            count: this.paginations.pageSize,
            index: this.paginations.pageIndex,
            search: {
              state: {
                $gt: 0
              }
            }
          });
        }
      });
    },

    formatState(row) {
      return row.state == 1 ? "正常" : row.state == 2 ? "维修" : "";
    },

    formatTime(row) {
      if (row.time) {
        return this.$tool.formatDate(new Date(row.time));
      }
    },

    edit() {
      var deit = this.$tool.deep(this.form);
      var del = {
        _id: deit._id,
        data: deit
      };
      delete del.data._id;
      edit(del).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "修改飞机数据成功！",
            type: "success"
          });
          this.planeList({
            count: this.paginations.pageSize,
            index: this.paginations.pageIndex,
            search: {
              state: {
                $gt: 0
              }
            }
          });
          this.dialogFormVisible = false;
        }
      });
    },

    add() {
      if (this.addType) {
        this.edit();
      } else {
        this.add2();
      }
    },

    add2() {
      if (!this.form.name || !this.form.fid || !this.form.state) {
        this.$message({
          message: "每一项都是必填项请将信息填写完整！",
          type: "warning"
        });
        return;
      }
      add({ data: this.form }).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "新增飞机成功！",
            type: "success"
          });
          this.planeList({
            count: this.paginations.pageSize,
            index: this.paginations.pageIndex,
            search: {
              state: {
                $gt: 0
              }
            }
          });
          this.form.name = "";
          this.form.fid = "";
          this.form.state = "";
          this.dialogFormVisible = false;
        }
      });
    },

    handleClick(row) {
      this.title = "修改飞机数据";
      row.state = row.state + "";
      this.form = row;
      this.addType = 1;
      this.dialogFormVisible = true;
    },

    planeList(obj) {
      var data = this.$tool.deep(obj);
      data.index -= 1;
      planeList(data).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          this.loading = false;
          this.paginations.total = list.total;
          this.tableData = list.array;
          let plane = list.array;
          // this.sessionData("set", "planeList", plane);

          // var planeData = this.sessionData("get", "planeList");
          // for (var i in planeData) {
          //   planeData[i].name = planeData[i].fid;
          // }
          // var arr = [];
          // for (var i in planeData) {
          //   if (planeData[i].state == 1) {
          //     arr.push(planeData[i]);
          //   }
          // }
          // this.sessionData("set", "planeList", arr);
        }
      });
      this.planeArr();
    },

    planeArr() {
      var data = {
        count: 1000,
        index: 0,
        search: {
          state: 1
        }
      };
      planeList(data).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          var planeData = list.array;
          for (var i in planeData) {
            planeData[i].name = planeData[i].fid;
          }
          var arr = [];
          for (var i in planeData) {
            if (planeData[i].state == 1) {
              arr.push(planeData[i]);
            }
          }
          this.sessionData("set", "planeList", arr);
        }
      });
    },

    // 每页多少条切换
    handleSizeChange(pageSize) {
      this.paginations.pageSize = pageSize;
      this.planeList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: {
            $gt: 0
          }
        }
      });
    },

    // 上下分页
    handleCurrentChange(page) {
      this.paginations.pageIndex = page;
      this.planeList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: {
            $gt: 0
          }
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import "./table.less";
/deep/.fjzt input{
  width: 380px;
}
/deep/.el-dialog {
  width: 570px;
  height: 370px;
  background: url("../../assets/img/userBg.png") no-repeat;
  background-size: 570px 370px;
}
