  width: 500px;
  margin-left: 0px;
}
/deep/.addPlane.el-button--primary{
  background: #2a6191;
  border-color: #2a6191;
}
.userButModle {
  width: 126px;
  height: 41px;
  color: #fff;
  font-size: 16px;
  border: none;
  background: url("../../assets/img/but.png") no-repeat;
}
.userButModle:first-child {
  margin-left: 0px;
  margin-top: 0px;
}
