  padding-bottom: 0px;
}
.userBut {
  font-size: 18px;
  color: #02c3c3;
  margin: 0 10px;
}
/deep/#customWidth > div {
  color: #fff;
}
/deep/.el-form-item__label,
/deep/.el-dialog__title {
  color: #52e5ff;
}
.fillcontain {
  padding-bottom: 0;
  width: 1648px;
  height: 980px;
  margin: 20px;
}
/deep/.search {
  border-radius: 0px;
}
/deep/.dialog-footer {
  text-align: center;
  margin-left: 45px;
}
.fillcontain > h3 {
  color: #52e5ff;
  font-size: 20px;
  font-weight: 500;
  width: 81px;
  position: relative;
  top: -8px;
  left: 65px;
}
.fillcontain {
  padding-bottom: 0;
  margin-left: 16px;
  height: 984px;
  background: url("../../assets/img/centerBg.png") no-repeat;
}
@media screen and (max-width: 1366px) {
  .fillcontain {
    background: url("../../assets/img/centerBg.png") no-repeat 0px 0px;
    background-size: 98% 100%;
    width: 98%;
  }
  .fillcontain > h3 {
    left: 26px;
  }
}
.fillcontain {
  padding-bottom: 0;
}
.contain {
  padding: 10px;
  margin-bottom: 20px;
}
.pagination {
  padding: 10px 20px;
  text-align: right;
}
.readWrapper {
  height: 50px;
}
.customWidth {
  width: 50%;
  left: 23%;
}
.addPlane {
  width: 40px;
  float: right;
  height: 23px;
  border-radius: 6px;
  margin-right: 21px;
  line-height: 10px;
  text-align: center;
  color: #fff600;
  margin-right: 3px;
  margin-bottom: 20px;
  position: relative;
    top: -15px;
    left: -70px;
}
/deep/.addPlane > span {
  position: relative;
  left: -14px;
  top: -1px;
}
</style>



